.game-selector {
  text-align: center;
}

.game-selector h2 {
  margin-bottom: 30px;
  color: #333;
  font-size: 2rem;
}

.game-modes {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-top: 30px;
}

.game-mode-card {
  background: white;
  border-radius: 15px;
  padding: 30px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  position: relative;
  overflow: hidden;
}

.game-mode-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.game-mode-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15);
  border-color: rgba(102, 126, 234, 0.3);
}

.game-mode-card:hover::before {
  transform: scaleX(1);
}

.game-mode-card h3 {
  margin: 0 0 15px 0;
  color: #333;
  font-size: 1.5rem;
  font-weight: 600;
}

.game-mode-card p {
  margin: 0 0 20px 0;
  color: #666;
  font-size: 1rem;
  line-height: 1.5;
}

.mode-icon {
  font-size: 3rem;
  margin-top: 20px;
  opacity: 0.8;
  transition: all 0.3s ease;
}

.game-mode-card:hover .mode-icon {
  transform: scale(1.1);
  opacity: 1;
}

/* 響應式設計 */
@media (max-width: 768px) {
  .game-modes {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .game-mode-card {
    padding: 25px;
  }
  
  .game-mode-card h3 {
    font-size: 1.3rem;
  }
  
  .mode-icon {
    font-size: 2.5rem;
  }
}

@media (max-width: 480px) {
  .game-selector h2 {
    font-size: 1.8rem;
  }
  
  .game-mode-card {
    padding: 20px;
  }
  
  .game-mode-card h3 {
    font-size: 1.2rem;
  }
  
  .mode-icon {
    font-size: 2rem;
  }
} 