.sorting-game {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.game-header {
  text-align: center;
  margin-bottom: 30px;
}

.game-header h2 {
  color: #333;
  margin-bottom: 10px;
}

.score-info {
  font-size: 16px;
  color: #666;
  font-weight: 500;
}

.question-section {
  background: #f8f9fa;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 30px;
  border-left: 4px solid #007bff;
}

.question-section h3 {
  margin: 0 0 15px 0;
  color: #333;
  font-size: 18px;
}

.hint-section {
  margin-top: 15px;
}

.btn-hint {
  background: #17a2b8;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s;
}

.btn-hint:hover {
  background: #138496;
}

.hint-text {
  margin-top: 10px;
  padding: 10px;
  background: #e3f2fd;
  border-radius: 5px;
  color: #1976d2;
  font-style: italic;
}

.sorting-area {
  margin-bottom: 30px;
}

.sorting-instructions {
  text-align: center;
  margin-bottom: 20px;
  color: #666;
  font-size: 16px;
}

.options-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
  max-width: 600px;
  margin: 0 auto;
}

.sorting-option {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px;
  background: white;
  border: 2px solid #e9ecef;
  border-radius: 10px;
  cursor: grab;
  transition: all 0.3s ease;
  position: relative;
}

.sorting-option:hover {
  border-color: #007bff;
  box-shadow: 0 4px 8px rgba(0, 123, 255, 0.1);
}

.sorting-option:active {
  cursor: grabbing;
}

.sorting-option.correct {
  border-color: #28a745;
  background: #f8fff9;
}

.sorting-option.incorrect {
  border-color: #dc3545;
  background: #fff8f8;
}

.option-number {
  width: 40px;
  height: 40px;
  background: #007bff;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 16px;
  flex-shrink: 0;
}

.sorting-option.correct .option-number {
  background: #28a745;
}

.sorting-option.incorrect .option-number {
  background: #dc3545;
}

.game-controls {
  text-align: center;
  margin-bottom: 30px;
}

.result-controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.result-message {
  font-size: 18px;
  font-weight: bold;
  padding: 10px 20px;
  border-radius: 5px;
}

.result-message.correct {
  color: #155724;
  background: #d4edda;
  border: 1px solid #c3e6cb;
}

.result-message.incorrect {
  color: #721c24;
  background: #f8d7da;
  border: 1px solid #f5c6cb;
}

.control-buttons {
  display: flex;
  gap: 15px;
  justify-content: center;
}

.correct-answer {
  background: #f8f9fa;
  padding: 20px;
  border-radius: 10px;
  border-left: 4px solid #28a745;
  margin-top: 20px;
}

.correct-answer h4 {
  margin: 0 0 15px 0;
  color: #28a745;
}

.correct-order {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.correct-option {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 10px;
  background: white;
  border-radius: 8px;
  border: 2px solid #28a745;
}

.correct-option .option-number {
  background: #28a745;
}

/* 拖拽時的視覺效果 */
.sorting-option.dragging {
  opacity: 0.5;
  transform: rotate(5deg);
}

.sorting-option.drag-over {
  border-color: #007bff;
  background: #f0f8ff;
}

/* 響應式設計 */
@media (max-width: 768px) {
  .sorting-game {
    padding: 15px;
  }
  
  .options-container {
    gap: 10px;
  }
  
  .sorting-option {
    padding: 12px;
    gap: 12px;
  }
  
  .option-number {
    width: 35px;
    height: 35px;
    font-size: 14px;
  }
  
  .control-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .control-buttons .btn {
    width: 200px;
  }
}

@media (max-width: 480px) {
  .sorting-option {
    flex-direction: column;
    text-align: center;
    gap: 10px;
  }
  
  .option-number {
    order: -1;
  }
} 